"use client";

import { useEffect, useState } from "react";

interface StatItem {
  label: string;
  value: string;
  suffix?: string;
  icon: React.ReactNode;
}

const stats: StatItem[] = [
  {
    label: "Total Donations",
    value: "10,000",
    suffix: "+",
    icon: (
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
      </svg>
    ),
  },
  {
    label: "Active Users",
    value: "5,000",
    suffix: "+",
    icon: (
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
      </svg>
    ),
  },
  {
    label: "Encryption Level",
    value: "256",
    suffix: "-bit",
    icon: (
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
      </svg>
    ),
  },
  {
    label: "Uptime",
    value: "99.9",
    suffix: "%",
    icon: (
      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
      </svg>
    ),
  },
];

export const StatsDisplay = () => {
  const [mounted, setMounted] = useState(false);
  const [animatedValues, setAnimatedValues] = useState<string[]>(stats.map(() => "0"));

  useEffect(() => {
    setMounted(true);
    
    // Animate numbers
    const duration = 2000;
    const steps = 60;
    const interval = duration / steps;
    
    let step = 0;
    const timer = setInterval(() => {
      step++;
      const progress = step / steps;
      const easeOut = 1 - Math.pow(1 - progress, 3);
      
      setAnimatedValues(
        stats.map((stat) => {
          const numericValue = parseFloat(stat.value.replace(/,/g, ""));
          const currentValue = Math.floor(numericValue * easeOut);
          return currentValue.toLocaleString();
        })
      );
      
      if (step >= steps) {
        clearInterval(timer);
        setAnimatedValues(stats.map((stat) => stat.value));
      }
    }, interval);
    
    return () => clearInterval(timer);
  }, []);

  if (!mounted) return null;

  return (
    <div className="w-full py-6">
      <div className="glass rounded-2xl p-6 animate-fade-in-up delay-200">
        <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
          {stats.map((stat, index) => (
            <div
              key={index}
              className="text-center group"
            >
              {/* Icon */}
              <div className="inline-flex items-center justify-center w-12 h-12 rounded-full bg-white/10 text-white mb-3 group-hover:scale-110 group-hover:bg-white/20 transition-all duration-300">
                {stat.icon}
              </div>
              
              {/* Value */}
              <div className="text-3xl font-bold text-white mb-1">
                {animatedValues[index]}
                <span className="text-purple-200">{stat.suffix}</span>
              </div>
              
              {/* Label */}
              <div className="text-white/60 text-sm">{stat.label}</div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};
